@import "../base/variables.less";

.navbar {
  background: @navbar-color;
  width: 100%;
  nav {
    max-width: 1280px;
    margin: 0px auto;
    display: flex;
    justify-content: flex-end;
    padding:0px 15px;

    ul {
      min-height: 80px;
      display: flex;
      align-items: center;
      gap: 35px;

      li {
        list-style-type: none;

        a {
          color: white;
          text-decoration: none;
          font-size: 20px;
          text-transform: uppercase;

          &:hover,
          &:focus {
            color: azure;
          }
        }
      }
    }
  }
}
